<template>
  <div class="studentList">
    <!-- 查询 -->
    <el-form :inline="true" :model="formInline" class="demo-form-inline" size="small">
      <el-form-item label="姓名" required>
        <el-input v-model="formInline.name" placeholder="姓名"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="find">查询</el-button>
        <el-button type="primary" @click="reset">重置</el-button>
      </el-form-item>
    </el-form>

    <!-- <el-table :data="tableData.slice((currentPage-1)*pageSize, currentPage*pageSize)" border style="width: 100%"> -->
    <el-table :data="compData" border style="width: 100%">
      <el-table-column prop="id" label="序号"> </el-table-column>
      <el-table-column prop="name" label="姓名" width="180"> </el-table-column>
      <el-table-column prop="age" label="年龄"> </el-table-column>
      <el-table-column prop="sex_text" label="性别"> </el-table-column>
      <el-table-column prop="number" label="学号"> </el-table-column>
      <el-table-column prop="class" label="班级"> </el-table-column>
      <el-table-column prop="state_text" label="	状态"> </el-table-column>
      <el-table-column prop="address" label="地址"> </el-table-column>
      <el-table-column prop="phone" label="	手机号码"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" circle></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="del(scope.row)"></el-button>
          <!-- <el-button type="text" size="small">编辑</el-button>
          <el-button type="text" size="small">删除</el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page=currentPage
      :page-sizes="[10, 20, 50, 100]"
      :page-size=pageSize
      layout="total, sizes, prev, pager, next, jumper"
      :total= total>
    </el-pagination>
  </div>
</template>

<script>
import { studentDel, students } from "@/api/api.js";

export default {
  data() {
    return {
      total: 0, //数据总条数
      currentPage: 1,   //当前页码
      pageSize: 10, //每页条数
      tableData: [],
      formInline: {
        name: null
      }
    };
  },

  created() {
    this.getData();
  },

  computed: {
    compData() {
      return this.tableData.slice((this.currentPage-1)*this.pageSize, this.currentPage*this.pageSize);
    }
  },

  methods: {
    getData(params) {
      students(params)
        .then((res) => {
          if (res.data.status === 200) {
            this.tableData = res.data.data;   
            this.tableData.forEach(item => {
              item.sex === 1 ? item.sex_text = '男' : item.sex_text = '女';
              item.state === '1' ? item.state_text = '已入学' : (item.state === '2' ? item.state_text = '未入学' : item.state_text = '休学中')
            })
            this.total = res.data.total;
            this.total = res.data.data.length;
    

            this.$message({ message: res.data.message, type: "success" });
          } else {
            this.$message(res.data.message);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    

    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.currentPage = 1;
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
    },

    //删除
    del(row){
      this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          studentDel(row.id)
            .then(res => {
              console.log(res);
              if(res.data.status === 200){
                this.$message({ type: 'success', message: '删除成功!' });
                this.getData(); //重新查询
              }else {
                this.$message.error('删除失败：'+res.data.message);
              }
            }).catch(err => {
              // this.$message({ type: 'error', message: '删除失败!'+err });
              this.$message.error('删除失败：'+err);
            })
          
          
        }).catch(() => {
          this.$message('取消删除');
        });
    },

    //查询
    find() {
      this.getData(this.formInline);
    },
    //重置
    reset() {
      this.formInline = {};
      this.getData();
    }
  }
}
</script>

<style lang="scss">
  .studentList {
    .demo-form-inline, .el-form-item {
      text-align: left;
    }
    .el-pagination {
      text-align: left;
      margin-top: 20px;
    }

  }
  
</style>